.Container {
    width: 100%;
    padding: 40px 0 20px 30px;
    display: flex;
    justify-content: flex-start;
    align-items: center
}
.Poem {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 10px 0 5px 0;
    font-weight: bold;
    font-size: 20px;
    text-align: center;
}
@keyframes opacity {
    0%{opacity: 1}
    50%{opacity: 0}
    100%{opacity: 1}
}
.Opacity{
    animation: opacity 2s ease-in forwards;
}

.Frame {
    padding: 40px 0 20px 30px;
}
.addAkaBtn {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 30px;
    font-size: 20px;
    font-weight: bold;
    border-radius: 3px;
    border: 1px solid green;
    background: lightgreen;
    color: #404040;
    cursor: pointer;
    margin: 10px 0 10px 0;
}
.addAkaBtn:hover {
    
    background:	 #65e765;
    border: 1px solid darkgreen;
    color: #d9d9d9;
}
.addLine {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100px;
    font-size: 14px;
    font-weight: bold;
    border-radius: 3px;
    border: 1px solid green;
    background: lightgreen;
    color: #404040;
    cursor: pointer;
    margin: 10px 0 10px 0;
}
.addLine:hover {
    
    background:	 #65e765;
    border: 1px solid darkgreen;
    color: #d9d9d9;
}
.goBackBtn {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 150px;
    font-size: 14px;
    font-weight: bold;
    border-radius: 3px;
    border: 1px solid green;
    background: lightgreen;
    color: #404040;
    cursor: pointer;
    margin: 20px 0 20px 0;
}
.goBackBtn:hover {
    
    background:	 #65e765;
    border: 1px solid darkgreen;
    color: #d9d9d9;
}
.deleteBtn {
    min-width: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 12px;
    border-radius: 3px;
    background: lighten(#FE6F5E,20%);
    color: #E12C2C;;
    border: 1px solid #E12C2C;
    margin: 10px 0 10px 0;
    cursor: pointer;
}
.deleteBtn:hover {
    background: #E12C2C;
    color: #d9d9d9;
    border: 1px solid #D92121;
}
.Akapit {
    margin: 20px 0 10px 0;
}
.Line {
    margin: 5px 0 5px 0;
}
.NewPoemBtn {
    display: flex;
    justify-content: center;
    align-items: center; 
    min-width: 120px;
    border-radius: 3px;
    border: 1px solid green;
    background: lightgreen;
    color: #404040;
    cursor: pointer;
    font-size: 16px;
    height: 30px;
}
.NewPoemBtn:hover {
    background:	 #65e765;
    border: 1px solid darkgreen;
    color: darken(#404040,12%);
}
.ContainerPoem, .ContainerReading {
    width: 100%;
    margin-top: 120px;
    padding: 40px 20px 20px 20px;
    display: flex;
    justify-content: center;
    align-items: center
}
.MyPoem {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        margin: 10px 0 5px 0;
        font-weight: bold;
        font-size: 20px;
}
.ContainerReading {
    flex-direction: column;
}